// frontend/src/Components/Signup.js
import React, { useState } from 'react';
import { NotificationContainer } from 'react-notifications';
import 'react-notifications/lib/notifications.css';
import { signup, login } from './auth.js';
import LoadingScreen from './loadingscreen.js';

import './css/signup.css';

const Signup = ({ setIsAuthenticated }) => {
  const [authMode, setAuthMode] = useState('signin');
  const [fullName, setFullName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [authState, setAuthState] = useState();

  const changeAuthMode = () => {
    setAuthMode(authMode === 'signin' ? 'signup' : 'signin');
  };

  const handleAuthSubmit = async(e) => {
    e.preventDefault();
    setAuthState("Passed");

    const authEndpoint = authMode === 'signin' ? 'login' : 'signup';    
    try{
      if (authEndpoint === 'signup') {
        await signup(fullName, email, password, setIsAuthenticated);
      } 
      else { 
        await login(email, password, setIsAuthenticated);
      }
    }
    catch(error){
      console.error('Bad Request:', error);
    }
    finally {
      setTimeout(() => {setAuthState("Failed");}, 100);
    }
};
  
  return (
    <div className="Auth-form-container">
      {authState === 'Passed' ? <LoadingScreen /> : ""}
      <form className={`Auth-form ${authMode === 'signup' ? 'signup' : ''}`} onSubmit={handleAuthSubmit} method="post">
        <div className="Auth-form-content">
          <h3 className="Auth-form-title">
            {authMode === 'signin' ? 'Sign In' : 'Sign Up'}
          </h3>
          <div className="text-center">
            {authMode === 'signin' ? (
              <>
                Not registered yet?{' '}
                <span className="link-primary" onClick={changeAuthMode}>
                  Sign Up
                </span>
              </>
            ) : (
              <>
                Already registered?{' '}
                <span className="link-primary" onClick={changeAuthMode}>
                  Sign In
                </span>
              </>
            )}
          </div>
          {authMode === 'signup' && (
            <div className="form-group mt-3">
              <label>Full Name</label>
              <input
                type="text"
                className="form-control mt-1"
                placeholder="e.g Jane Doe"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
              />
            </div>
          )}
          <div className="form-group mt-3">
            <label>Email address</label>
            <input
              type="email"
              className="form-control mt-1"
              placeholder="e.g janedoe@example.com"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </div>
          <div className="form-group mt-3">
            <label>Password</label>
            <input
              type="password"
              className="form-control mt-1"
              placeholder="At least 8 characters"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
          </div>
          <div className="d-grid gap-2 mt-3">
            <button type="submit" className="btn btn-primary">
              {authMode === 'signin' ? 'Sign In' : 'Sign Up'}
            </button>
          </div>
          {authMode === 'signin' && (
            <p className="mt-2">
              <a href="#">Forgot password?</a>
            </p>
          )}
        </div>
      </form>
      <NotificationContainer position="top-left" />
    </div>
  );
};

export default Signup;
